.filters {
  width: 100%;
  height: 5vh;
  z-index: 1;
  background-color: #000;
  border-top: .3vw solid #000;
  border-bottom: .3vw solid #000;
  grid-template-columns: repeat(5, minmax(max-content, 1fr));
  gap: .3vw;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
