  cursor: pointer;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: .5em;
  font-size: 1.2vw;
  display: flex;
}

.filter:hover .select {
  color: red;
}

main {
  background-color: #000;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-auto-rows: max-content;
  gap: .3vw;
  margin-top: 5vh;
  display: grid;
}

